"use client";

import { motion } from "framer-motion";
import Section from "@/components/Section";
import { certifications } from "@/data/certifications";
import { Award, ExternalLink } from "lucide-react";

export default function CertificationsSection() {
  if (certifications.length === 0) {
    return (
      <Section id="certifications" title="Certifications">
        <div className="text-center text-gray-600 dark:text-gray-400">
          <p>Certifications will be displayed here.</p>
          <p className="text-sm mt-2">Please add your certifications in data/certifications.ts</p>
        </div>
      </Section>
    );
  }

  return (
    <Section id="certifications" title="Certifications" subtitle="Professional credentials">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {certifications.map((cert, index) => (
          <motion.div
            key={cert.id}
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, margin: "-50px" }}
            transition={{ duration: 0.5, delay: index * 0.1 }}
            className="bg-white/60 dark:bg-gray-800/60 backdrop-blur-sm rounded-xl p-6 shadow-lg hover:shadow-xl transition-shadow duration-300"
          >
            <div className="flex items-start gap-4">
              <div className="p-3 bg-blue-100 dark:bg-blue-900/30 rounded-lg">
                <Award className="w-6 h-6 text-blue-600 dark:text-blue-400" />
              </div>
              <div className="flex-1">
                <h3 className="text-xl font-bold text-gray-900 dark:text-white mb-1">
                  {cert.name}
                </h3>
                <p className="text-blue-600 dark:text-blue-400 font-medium mb-2">
                  {cert.issuer}
                </p>
                <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
                  Issued: {cert.issueDate}
                  {cert.expirationDate && ` â€¢ Expires: ${cert.expirationDate}`}
                </p>
                {cert.credentialId && (
                  <p className="text-sm text-gray-500 dark:text-gray-500 mb-3">
                    ID: {cert.credentialId}
                  </p>
                )}
                {cert.technologies && cert.technologies.length > 0 && (
                  <div className="flex flex-wrap gap-2 mb-3">
                    {cert.technologies.map((tech) => (
                      <span
                        key={tech}
                        className="px-2 py-1 bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded text-xs"
                      >
                        {tech}
                      </span>
                    ))}
                  </div>
                )}
                {cert.credentialUrl && (
                  <a
                    href={cert.credentialUrl}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="inline-flex items-center gap-1 text-sm text-blue-600 dark:text-blue-400 hover:underline"
                  >
                    Verify Credential
                    <ExternalLink size={14} />
                  </a>
                )}
              </div>
            </div>
          </motion.div>
        ))}
      </div>
    </Section>
  );
}

